import axios from 'axios';
import { authHeader } from './authservice/auth-header';

const API_URL = process.env.VUE_APP_BASE_API_URL;

class PreferenceService {
    getPreference(id) {
        return axios.get(API_URL+'preferences/'+id, { headers: authHeader() });
    }
    updatePreference(id, data) {
        return axios.patch(API_URL+'preferences/'+id, data, { headers: authHeader() });
    }
}

export default new PreferenceService();
