<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row mb-4">
      <div class="col-12">
        <div class="card">
          <div class="card-body">
            <div class="row mt-5 mb-5 pb-4 mx-auto" v-if="loading">
                <div class="col-12 p-5 m-4 text-center">
                    <div class="spinner-border text-secondary" role="status" v-if="loading">
                        <span class="sr-only">Loading..</span>
                      </div>
                </div>
            </div>
            <div v-if="!loading">
            <div v-if="user_type == 'su' || user_type == 'adm'">
                 <h5 class="font-weight-bold mb-4">Pusher</h5>
                      <div class="table-responsive" >
                          <table class="table bordered table text-left">
                          <thead>
                              <tr>
                                  <td width="25%"><b>Name</b></td>
                                  <td width="70%">
                                      <input
                                      type="text"
                                      class="form-control bg-light"
                                        v-model="tableData.preference_name"
                                      />
                                  </td>
                              </tr>
                              <tr>
                                  <td width="25%"><b>Api ID</b></td>
                                  <td width="70%">
                                    <input
                                      type="text"
                                      class="form-control bg-light"
                                      v-model="tableData.app_id"
                                      />
                                  </td>
                              </tr>

                              <tr>
                                  <td width="25%"><b>Api Key</b></td>
                                  <td width="80%">
                                    <input
                                      type="text"
                                      class="form-control bg-light"
                                      v-model="tableData.app_key"
                                      />
                                  </td>
                              </tr>
                              <tr>
                                  <td width="25%"><b>App Secret</b></td>
                                  <td width="80%">
                                    <input
                                      type="text"
                                      class="form-control bg-light"
                                      v-model="tableData.app_secret"
                                      />
                                  </td>
                              </tr>
                              <tr>
                                  <td width="25%"><b>App Cluster</b></td>
                                  <td width="80%">
                                    <input
                                      type="text"
                                      class="form-control bg-light"
                                      v-model="tableData.app_cluster"
                                      />
                                  </td>
                              </tr>
                              
                                <tr>
                                  <td width="0%"></td>
                                  <td width="100%" class="text-right">
                                    <div>
                                      <b-button class="btn" variant="success ml-1" @click="updatePreference()">{{ $t("all.update") }} </b-button>
                                    </div>
                                  </td>
                              </tr>
                          </thead>
                          </table>
                      </div>
            </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
import Vue from "vue";
import Swal from "sweetalert2";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";
import { required, sameAs } from "vuelidate/lib/validators";
import UserService from "@/helpers/user.service";
import PreferenceService from "@/helpers/preference.service";
import store from '@/state/store';
import moment from "moment";

export default {
  page: {
    meta: [{ name: "description", content: appConfig.description }],
  },
  components: { Layout, PageHeader },
  data() {
    return {
      urlImage: process.env.VUE_APP_BASE_API_URL_AVATAR,
      user_type: store.state.authBackend.user.type,
      title: this.$t("preference.preference-management"),
      items: [
        {
          text: this.$t("menuitems.dashboards.text"),
          href: "#",
        },
        {
          text: this.$t("preference.preference-management"),
          active: true,
        },
      ],
      loading:false,
      tableData:[{
        "id_preference": '',
        "preference_name": '',
        "app_id": '',
        "app_key": '',
        "app_secret": '',
        "app_cluster": '',
        "created_at": '',
        "updated_at": ''
      }],
      submitted: false,
    };
  },
  validations: {
    form: {
        preference_name: { required },
        preference_value: { required }
    },
  },
  computed: {
    rows() {
      return this.tableData.length;
    },
  },
  methods: {
    select() {
        this.selected = [];
        if (!this.selectAll) {
            for (let i in this.tableData) {
                this.selected.push(this.tableData[i].id_preference);
            }
        }
    },
    list() {
      // this.loading = true;
      UserService.getProfile()
      .then(response => {
        // console.log( response.data.data)
        this.user_type = response.data.data.admin.role
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    getPref() {
      this.loading = true;
      let id = 'pusher';
      PreferenceService.getPreference(id)
        .then((response) => {
            this.tableData = [];
            // this.tableData = response.data.data;
            let value = response.data.data.preference_value;
            let val = JSON.parse(value);
            console.log(val );
            this.tableData = {
                      "id_preference" : response.data.data.id_preference,
                      "preference_name" : response.data.data.preference_name,
                      "app_id": val.app_id,
                      "app_key": val.app_key,
                      "app_secret": val.app_secret,
                      "app_cluster": val.app_cluster,
                      "created_at": response.data.data.created_at,
                      "updated_at": response.data.data.updated_at
                    },
            this.loading = false;
        })
        .catch((err) => {
            this.loading = false;
            console.log(err);
        });
    },
     getPrefUpdate() {
      let id = 'pusher';
      PreferenceService.getPreference(id)
        .then((response) => {
            this.tableData = [];
            // this.tableData = response.data.data;
            let value = response.data.data.preference_value;
            let val = JSON.parse(value);
            console.log(val );
            this.tableData = {
                      "id_preference" : response.data.data.id_preference,
                      "preference_name" : response.data.data.preference_name,
                      "app_id": val.app_id,
                      "app_key": val.app_key,
                      "app_secret": val.app_secret,
                      "app_cluster": val.app_cluster,
                      "created_at": response.data.data.created_at,
                      "updated_at": response.data.data.updated_at
                    },
            this.loading = false;
        })
        .catch((err) => {
            this.loading = false;
            console.log(err);
        });
    },

    updatePreference() {
        let data = {
              "name": this.tableData.preference_name,
              "value" : {
              "app_id" : this.tableData.app_id,
              "app_key" : this.tableData.app_key,
              "app_secret" : this.tableData.app_secret,
              "app_cluster" : this.tableData.app_cluster
              }
        }
        console.log(data);
        PreferenceService.updatePreference(this.tableData.preference_name, data)
          .then((response) => {
            Vue.swal({
              position: "top-end",
              icon: "success",
              title: response.data.message,
              showConfirmButton: false,
              timer: 1500,
            });
            this.getPrefUpdate();
          })
          .catch((err) => {
            console.log(err);
          });
    },
    formatDate(value) {
      return moment(value).format("YYYY-MM-DD, hh:mm");
    },
  },
  beforeMount(){
    this.list();
  },
  mounted() {
    this.getPref();
  },
};
</script>

<style scoped>
.badge {
  border-radius: 2%;
}

</style>
